@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;400;500;700&display=swap');

*{
    margin: 0;
    padding: 0;
    
    font-family: 'Poppins', sans-serif;
    -ms-overflow-style: none;  /* IE and Edge */
    scrollbar-width: none;  /* Firefox */
    ::-webkit-scrollbar{
        display: none;
    }

    
}

.header-section{
    
    display: flex;
    align-items: center;
    justify-content: left;
    padding-top: 20px;
    padding-left: 30px;
    width: 90%;
    user-select: none;
    margin: auto;
   
}
.pokeicon{
    cursor: pointer;
    width: 60px;
    height: 60px;
    padding-right: 20px;
}
.main-title{
    cursor: pointer;
    font-size: 30px;
    color: rgb(37, 37, 37);
}

//=------------------------------------------------------------------------------search SECTION
.search-section{
    width: 90%;
    margin: auto;
    padding-block: 15px;
    display: grid;
    grid-template-columns: 90% 10%;
   

    .search-input-section{
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        border: none;
       

        .search-bar{
            background-color:#252525;
            color: white;
            font-size: larger;
            display: flex;
            align-items: center;
            justify-content: center;
            width: 100%;
            height: 7vh;
            outline: none;
            border: none;
            padding-left: 15px;
            border-radius: 7px 0px 0px 7px;

            ::placeholder{

                font-size: larger; 
            }
        }
    }
}

.search-icon-section{
    background-color:#252525;
    display: flex;
    justify-content: center;
    align-items: center;
    border: none;
    border-radius: 0px 7px 7px 0px;
    height: 7vh;

    .search-icon{
        color: white;
    }

    .clear-icon{
        cursor: pointer;
        height: 5vh;
        color: rgb(255, 193, 193);

        :hover{
            transition: 300ms;
            color: red;
        }
    }

}
 













.not-found{
    width: 50%;
    margin: auto;
    font-size: large;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
}

.dataResult{
    width: 90%;
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    background-color: #96D9D6;
    margin: auto;
    height: 15vh;
    overflow: hidden;
    overflow-y:scroll;
    scrollbar-width: none;
   
}
.dataResult::-webkit-scrollbar{
    display: none;
    scrollbar-width: none;
}



.pokemon-items{
    
    font-size: medium;
    text-decoration: none;
    color: #6F35FC;
    :hover{
        cursor: pointer;
    }
}


//------------------------------------------------------------------------------------------CARD CSS DETAILS---------------


.rendered-card{
    opacity: 0;
}

.type-img{
    width: 40px;
    height: 40px;
}
.pokemon-card-img{
    width: 80px;
    height: 80px;
}

.card-info{
    width: 90%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    
}

.card-number{
    font-size: xx-large;
    font-weight: 700;
    animation-name: card-number;
    animation-duration: 1s;

    
}
@keyframes card-number{
    0%{opacity: 0;
        position: relative;
    margin-left: 5px;
    overflow: hidden;}

    100%{opacity: 1;
        position: relative;
    margin-left: 0px;
    overflow: hidden;}
}

.card-name{
    font-size: x-large;
    font-weight: 700;
    animation-name: card-name;
    animation-duration: 1s;
}
@keyframes card-name{
    0%{opacity: 0;
        position: relative;
    margin-left: 5px;
    overflow: hidden;}


    100%{opacity: 1;
        position: relative;
    margin-left: 0px;
    overflow: hidden
    }

}



//-----------------------------------------------------------------------------------------------------------------------------
.nav-button-container{
    font-size: larger;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 10px;
    user-select: none;
}

.buttons{
    user-select: none;
    width: 90%;
    margin: auto;
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding-block: 15px;
}

.nav-button{
    user-select: none;
    padding-left: 5px;
    padding-right: 5px;
    
}

.body-container{
    width: 90%;
    height: 100%;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(10, 1fr);
    grid-column-gap: 4px;
    grid-row-gap: 4px;
    text-align: center;
    align-items: center;
    margin: auto;
   
    

    
    
}






.loader{
    width: 10%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: auto;

}




// ------------------------------------------------------------------------------card colors

.pokemonType{
    border-radius: 8px;
    cursor: pointer;
    display: flex;
    width: 100%;
    padding-block: 10px;
    margin: auto;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    color: rgb(235, 235, 235);
   animation-name: card-fade;
   animation-duration: 1s;
    
}
@keyframes card-fade{
    from{opacity: 0;
       
   }
    to{ opacity: 1}
}

.pokemonTypeSkeleton{
 border-radius: 7px;
 height: 170px;
 width: 100%;

 background-color:rgb(226, 226, 226);
 display: flex;
 justify-content: center;
 align-items: center;
 flex-direction: column;
 justify-content: space-between;
 
}

@keyframes skeleton-loading{
    0%{
        background-color:transparent;
    }
    100%{
        background-color: rgb(202, 202, 202);
    }
}


.loadingInfo{
width: 90%;
background-color: transparent;
height: 20%;
margin-top: 10px;
display: flex;
justify-content: space-between;
padding-left: 10px;
padding-right: 10px;
align-items: center;
border-radius: 7px;

.loadingNumber{
    border-radius: 7px;
    width: 20%;
    height: 100%;
    animation: skeleton-loading 500ms linear infinite alternate;
}

.loadingType{
    width: 11%;
    height: 100%;
    animation: skeleton-loading 500ms linear infinite alternate;
    border-radius: 50%;
}



}
.loadingImg{
   
    animation: skeleton-loading 500ms linear infinite alternate;
    height: 80px;
    width: 80px;
    margin-block: 2px;
    border-radius: 7px;
    
}
.loadingName{
    width: 30%;
    animation: skeleton-loading 500ms linear infinite alternate;
height: 20px;
padding-block: 10px;
margin-bottom: 10px;
border-radius: 7px

}






