//============================================DETAIL SECTION CSS=========================


.detail-main{
margin-top: -50px;
position: fixed;
width: 100%;
height: 50vh;
-webkit-border-bottom-right-radius: 500px;
-webkit-border-bottom-left-radius: 500px;
-moz-border-radius-bottomright: 500px;
-moz-border-radius-bottomleft: 500px;
border-bottom-right-radius: 500px;
border-bottom-left-radius: 500px;


    
}

.info{
    
    position: relative;
    padding-left: 15px;
    padding-block: 10px;
    font-size: 20px;
    color: white ;
    font-weight: 700;
    text-shadow: 2px 2px #aaaaaa;

    .info-name{
        padding-top: 35px;
    }
}

.about{
    padding-block: 20px;
    width: 90%;
    display: flex;
    justify-content: space-evenly;
    margin: auto;
    flex-direction: column;
   
}

.about-main-title{
    width: 90%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: auto;
    font-size: x-large;
    font-weight: 500;
    color:rgb(117, 117, 117)
}

.detail-stats{
    @extend .about-main-title;
    padding-block: 1em;
}

.about-weight{
    padding-block: 6px;
    padding-left: 15px;
    font-size: 22px;
    color: rgb(44, 44, 44);
    display: flex;
    justify-content: flex-start;
    align-items: center;

    .about-icon{
        color:rgb(136, 136, 136) ;
        margin-right: 10px;
    }

    .about-title{
        font-size: medium;
        margin-right: 10px;
        font-weight: 500;
    }

    .stat-value{
        font-size: medium
    }
   

}

.about-height{
    @extend .about-weight;
    
    .about-icon{
        margin-left: 5px;
        margin-right: 10px;
    }

    .about-title{
        margin-left: 6px;
        font-size: medium;
        margin-right: 10px;
    }
}

.about-move {
    @extend .about-weight;
    
    .about-icon{
        margin-left: 1px;
        margin-right: 10px;
    }

    .about-title{
        margin-left: 3px;
        font-size: medium;
        margin-right: 10px;
    }

}



.goback-icon{
    cursor: pointer;
    border-radius: 12px red;
    position: absolute;
    font-size: xx-large;
    margin-bottom: 25px;
   
    :hover{
        opacity: 0.5;
        transition: 350ms;
    }
}

.about-stats{
    display: flex;
    justify-content: space-around;

}


.images{
    width: 100%;
    padding-top: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    position: fixed;
  
}
.detail-pokeball-img{
