//---------------------------------------------------STATS- CSS ------
.stat-container{
    cursor: default;
    display: grid;
    grid-template-columns: 15% 85%;
    grid-template-rows: 1fr;
    grid-column-gap: 0px;
    grid-row-gap: 0px; 
    margin-top: 5px;
    text-align: end;
        
}
.stat-title{
    font-size: large;
    font-weight: 500;
    margin-top: 8px;
    color: #585858;
}

.bar-stat-base{
    width: 90%;
    font-size: x-large;
    font-weight: 900;
    letter-spacing: -14px;
    background-color: rgb(231, 231, 231);
    border-radius: 7px;
    height: 25px;
    margin: auto;
    text-align: start;
    overflow-y: hidden;
   
    
    
    
}

.move-names{
