 /* Extra small devices (phones, 600px and down) */
 @media only screen and (max-width: 400px) {

    .body-container{
       
        grid-template-columns: repeat(1, 1fr);
       
    }
    .pokemon-img{
        width: 330px;
        height: 330px;
    }

    .stat-title{
        font-size: medium;
        font-weight: 500;
        margin-top: 8px;
        color: #585858;
